import React from "react";
import axios from "axios";
import School from "./School";

class App extends React.Component {
  state = {
    isLoading : true,
    school: []
  };
  getSchool = async() => {
    const {data:{schoolInfo:[,{row:school}]}} = await axios.get("https://open.neis.go.kr/hub/schoolInfo?Type=json&KEY=2635a956029947e4bb9bf6809c966470&pIndex=1&pSize=100&ATPT_OFCDC_SC_CODE=P10");
    console.log(school);
    this.setState({school : school, isLoading: false});
  };
  componentDidMount(){
    this.getSchool();
  }
  render(){
    const {isLoading,school} = this.state;
    return <div>{isLoading ? "Loading..." : school.map(schools => (
      <School
        code={schools.SD_SCHUL_CODE}
        name={schools.SCHUL_NM}
        site={schools.HMPG_ADRES}
      />
    ))}</div>;
  }
}

export default App;
